input {
  background: #eceff8;
  border: 2px solid #eceff8;
  // height: 45px;
  box-shadow: none;
  padding-left: 10px;
  font-size: 14px;
  color: #333;
  // width: 100%;
}

select {
  width: 100%;
  background: transparent;
  border: 0 solid #eceff8;
  box-shadow: none;
  font-size: 14px;
  color: #333;
}

option {
  background: #fff;
  border: 0 solid #626262;
  padding-left: 10px;
  font-size: 14px;
}

input:focus {
  background: transparent;
}
.validmonth input:focus{
  box-shadow: none;

}
textarea {
  resize: vertical;
  background: #eceff8;
  border: 2px solid #eceff8;
  padding: 10px;
  width: 100%;
  font-size: 14px;
}

textarea:focus {
  background: transparent;
  border: 2px solid #4fc1f0;
  outline: none;
}

.input-group.divcenter.input-group .form-control {
  padding-left: 0;
}

.custom-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  @media #{$xl-layout} {
    margin-right: -15px;
    margin-left: -15px;
  }
  @media #{$lg-layout} {
    margin-right: -15px;
    margin-left: -15px;
  }
  @media #{$md-layout} {
    margin-right: -15px;
    margin-left: -15px;
  }
  @media #{$xs-layout} {
    margin-right: -15px;
    margin-left: -15px;
  }
}

