.scroll-top {
  position: fixed;
  width: 50px;
  height: 50px;
  background: none;
  border: none;
  background-color: $theme-color;
  color: #fff;
  right: 20px;
  bottom: 60px;
  text-align: center;
  overflow: hidden;
  border-radius: 50px;
  z-index: 9811 !important;
  visibility: hidden;
  opacity: 0;
  &.show {
    visibility: visible;
    opacity: 1;
  }
  @media #{$xs-layout} {
    width: 40px;
    height: 40px;
  }
  & i {
    display: block;
    line-height: 50px;
    font-size: 22px;
    @media #{$xs-layout} {
      line-height: 40px;
      font-size: 16px;
    }
  }
  &:hover {
    & i {
      animation-name: fadeInUp;
      animation-duration: 1s;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
    }
  }
}

