/*---- 17. Sidebar style  ------*/

.sidebar-style {
  @media #{$lg-layout} {
    &.mr-30 {
      margin-right: 0;
    }
  }

  @media #{$md-layout} {
    &.mr-30 {
      margin-right: 0;
    }

    margin-top: 30px;
  }

  @media #{$xs-layout} {
    &.mr-30 {
      margin-right: 0;
    }

    margin-top: 30px;
  }

  @media #{$lg-layout} {
    &.ml-30 {
      margin-left: 0;
    }
  }

  @media #{$md-layout} {
    &.ml-30 {
      margin-left: 0;
    }

    margin-top: 30px;
  }

  @media #{$xs-layout} {
    &.ml-30 {
      margin-left: 0;
    }

    margin-top: 30px;
  }
}

