.pro-sidebar-search {
  .pro-sidebar-search-form {
    position: relative;

    input {
      background: transparent none repeat scroll 0 0;
      border: 1px solid #e6e6e6;
      color: #000;
      font-size: 14px;
      height: 43px;
      padding: 2px 55px 2px 18px;
    }

    button {
      background: transparent none repeat scroll 0 0;
      border-color: #a1a5aa;
      border-image: none;
      border-style: none none none solid;
      border-width: medium medium medium 1px;
      padding: 0 15px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      color: #000;
      transition: all 0.3s ease 0s;
      font-size: 20px;

      &:hover {
        color: $theme-color;
      }
    }
  }
}

.single-sidebar-blog {
  border-bottom: 1px solid #dee0e4;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 20px;
  padding: 0 0 20px;
  align-items: center;

  &:last-child {
    border-bottom: none;
  }

  .sidebar-blog-img {
    flex: 0 0 70px;
    margin: 0 20px 0 0;

    @media #{$lg-layout} {
      margin: 0 8px 0 0;
    }

    img {
      width: 100%;
    }
  }

  .sidebar-blog-content {
    span {
      color: #333;
      display: block;
      font-size: 12px;
      letter-spacing: 1px;
      text-transform: uppercase;
      font-weight: 400;
      line-height: 1;
    }

    h4 {
      font-size: 14px;
      margin: 5px 0 0;
    }
  }
}